import React, { useEffect, useState } from 'react';
import {blogData} from "../../utils/commonData";
import "./index.css";
import moment from 'moment';
const Blog=()=>{
    const [blogs,setBlogs]=useState([])
   useEffect(()=>{
       
      setBlogs(blogData)
   })
   return(
    <div className="blogs">
    {blogs.length && blogs.map(({ authorname, id, small_image, posted_on, slug, description, title }) =>
      <div key={id} className="blog">        
        <img src={small_image} alt="slug-logo"/>
        <div className="blog-text">
        <h3 className="blog-title">{title}</h3>
        <h5 className="blog-author">{authorname} | <span>{moment.utc(posted_on).format("DD MMM YYYY")}</span></h5>
        <p className="blog-discription">{description}</p>
        
       </div>
     </div>
    )}
  </div>
   )
}
export default Blog