import React, { useEffect, useState } from 'react';
import {filterData} from "../../utils/commonData"
import "./index.css";
const Filter=()=>{
    const [filters,setFilters]=useState([]);
    const [clicked,setclicked]=useState(0);
   useEffect(()=>{
       
      setFilters(filterData)
   })
   const onFilterClicked=(id,title)=>{
      
        setclicked(id);
   }
    return(
        <div className="latest-post">
            <p className="post-head">Latest Posts</p>
            <div className="filterText">
                <img src="data:image/png;base64," style={{width:"30px",height:"30px"}} alt="filterLogo" className="filter-logo"/>
                <p>Filter by Category</p>
            </div>
        <div className="capsules">
            {/* <div className={clicked? "capsule":"capsule-active"} >All</div> */}
         {filters.length && filterData.map(({ id, title }) => <div className={clicked === id?"capsule-active":"capsule"} onClick={()=>onFilterClicked(id,title)} key={id}>{title}</div>)}
        </div>
      </div>
      ) 
}
export default Filter